import { useForm } from 'react-hook-form';
import { useRef, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

// import component
import ModalContent from './ModalContent';
import UserAvatar from './UserAvatar';
import UserBk from './UserBk';
import InputGroup from '../forms/InputGroup';
import { ModalHeaderIcon } from './ModalHeader';

import { getUserInfo, updateUserInfo } from '../../api/userinfo';
import { useAuth } from '../../components/context/AuthContext';
import Swal from 'sweetalert2';
import 'sweetalert2/src/sweetalert2.scss';

//import svg
import iconCamera from '../../assets/images/icon/addphoto.svg';
import iconClose from '../../assets/images/icon/close.svg';
import iconNotiSuccess from '../../assets/images/icon/alert-success-2.svg';
import iconNotiWanrning from '../../assets/images/icon/alert-warning-2.svg';

const defaultBk = 'https://i.imgur.com/ZFz8ZEI.png';
// const defaultAvatar = 'https://i.imgur.com/V4RclNb.png';
// @ 
const formData = new FormData();

const Modal = () => {
  const { isAuthentic, member, setModalProOpen } = useAuth(); // 
  const [profile, setProfile] = useState({
    name: '',
    introduction: '',
    avatar: '',
    banner: '',
  });
  const [isUpdating, setIsupdating] = useState(false); //update-profile-loading
  const [isLoading, setIsLoading] = useState(false); //Profile-loading
  const navigate = useNavigate();
  // toast 
  const ToastSuccess = Swal.mixin({
    toast: true,
    html: `<div>
    <img src="${iconNotiSuccess}" class="icon-alert-noti"/>
    </div>`,
    showConfirmButton: false,
    position: 'top',
    width: '394px',
    // height: '96px',
    timer: 3000,
    timerProgressBar: false,
    showClass: {
      popup: 'animate__animated animate__fadeInDown',
    },
    hideClass: {
      popup: 'animate__animated animate__fadeOutUp',
    },
  });

  const ToastWarning = Swal.mixin({
    toast: true,
    html: `<div>
    <img src="${iconNotiWanrning}" class="icon-alert-noti"/>
    </div>`,
    showConfirmButton: false,
    position: 'top',
    width: '394px',
    height: '96px',
    timer: 3000,
    timerProgressBar: false,
    showClass: {
      popup: 'animate__animated animate__fadeInDown',
    },
    hideClass: {
      popup: 'animate__animated animate__fadeOutUp',
    },
  });

  //@ upload photo
  const uploadedImage = useRef(null); // 
  const imageUploader = useRef(null); // input file avatar
  const imageUploader_bk = useRef(null); // input file bk
  const [imageNewUrl, setImageNewUrl] = useState(''); //avtar
  const [imageNewUrl_bk, setImageNewUrl_bk] = useState(''); //bk
  const [imageChanging, setImageChanging] = useState(false);

  // using react-form-hook-set-up
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
    watch,
    setValue,
  } = useForm({
    defaultValues: {
      name: profile.name,
      introduction: profile.introduction,
      avatar: profile.avatar,
      banner: profile.banner,
    },
  });

  //  /api/users/id
  const getUserInfoAsync = async () => {
    setIsLoading(true);
    try {
      const profile = await getUserInfo(member.id);
      if (profile) {
        setProfile(profile);
        setImageNewUrl_bk(profile.banner);
        setImageNewUrl(profile.avatar);
        setIsLoading(false);
      }
    } catch (error) {
      console.error('[getUser Info  with Async failed]', error);
      setIsLoading(false);
    }
  };

  //form
  const onSubmit = async (data) => {
    try {
      //@ input
      if (formData.get('name')) {
        formData.delete('name');
      }
      formData.append('name', data.name);

      if (formData.get('introduction')) {
        formData.delete('introduction');
      }

      formData.append('introduction', data.introduction);

      setIsupdating(true);
      const res = await updateUserInfo({
        id: member.id,
        // data: data,
        img: formData,
      });

      if (res.status === 200) {
        // console.log('SUCCESS!');
        setModalProOpen(false);
        setIsupdating(false);

        ToastSuccess.fire({
          title: '!',
        });
      } else {
        setModalProOpen(true);
        ToastWarning.fire({
          title: `,jpg/png/gif`,
        });
      }
      for (var key of formData.keys()) {
        // console.log(key);
        formData.delete(key);
      }
    } catch (error) {
      ToastWarning.fire({
        title: `${error}`,
      });
    }
  };

  // @+formData
  const handleImageUpload = (e) => {
    // console.log(e.target.className);

    //@formDatainput file avatar/bkfile
    const avatarFile = imageUploader.current.files[0];
    const bannerFile = imageUploader_bk.current.files[0];

    // @-(className)
    let curr_target = 'input-file-avatar';
    if (e.target.className === 'input-file-bk') {
      curr_target = 'input-file-bk';

      // formDataformData,
      if (formData.get('banner')) {
        formData.delete('banner');
      }
      formData.append('banner', bannerFile);
    } else {
      // formDataformData,
      if (formData.get('avatar')) {
        formData.delete('avatar');
      }
      formData.append('avatar', avatarFile);
    }

    // @-base64
    const [file] = e.target.files;
    if (file) {
      const reader = new FileReader();
      const { current } = uploadedImage;
      current.file = file;
      reader.onload = (e) => {
        if (curr_target === 'input-file-avatar') {
          //imageUrl
          setImageNewUrl(e.target.result);
          //input react-hook-form
          // setValue('avatar', e.target.result);
        } else if (curr_target === 'input-file-bk') {
          //imageUrl
          setImageNewUrl_bk(e.target.result);
          setImageChanging(true);
          //input react-hook-form
          // setValue('banner', e.target.result);
        }
      };
      // base64
      reader.readAsDataURL(file);
    }
  };

  //
  const handleImageDelete = (e) => {
    // alert('');
    setImageNewUrl_bk(profile.banner ? profile.banner : defaultBk);
    //form value
    setValue('banner', profile.banner ? profile.banner : defaultBk);
    //
    setImageChanging(false);
  };

  //@ profile
  useEffect(() => {
    getUserInfoAsync();
  }, []);

  // @ profile formreset
  useEffect(() => {
    reset({ ...profile });
  }, [profile, reset]);

  useEffect(() => {
    if (!isAuthentic) {
      navigate('/login');
    }
  }, [navigate, isAuthentic]); //isAuthenticnavigation

  return (
    <>
      <ModalContent>
        <ModalHeaderIcon
          setModalProOpen={setModalProOpen}
          onSubmit={onSubmit}
          isUpdating={isUpdating}
        />
        <div className='modal-content'>
          <div className='profile-bk-wrapper'>
            <UserBk bkUrl={imageNewUrl_bk} isLoading={isLoading} />
            <img
              alt='bk-camera'
              src={iconCamera}
              className='icon-camera'
              ref={uploadedImage}
              onClick={() => imageUploader_bk.current.click()}
            />
            {imageChanging && (
              <img
                src={iconClose}
                alt='icon of close button'
                className='icon-close'
                onClick={handleImageDelete}
              />
            )}

            <input
              type='file'
              accept='image/*'
              onChange={handleImageUpload}
              ref={imageUploader_bk}
              className='input-file-bk'
              //@ sam  name='banner'
              name='banner'
              id='banner'
            />
          </div>

          <div className='avatar-edit-wrapper'>
            <UserAvatar avatar={imageNewUrl} isLoading={isLoading} />
            <img
              alt='bk-camera'
              src={iconCamera}
              className='icon-camera'
              ref={uploadedImage}
              onClick={() => imageUploader.current.click()}
            />

            <input
              type='file'
              accept='image/*'
              onChange={handleImageUpload}
              ref={imageUploader}
              className='input-file-avatar'
              //@ sam  name='avatar'
              name='avatar'
              id='avatar'
            />
          </div>

          <form
            className='modal-info-form'
            id='hook-form'
            onSubmit={handleSubmit(onSubmit)}
          >
            <div className='input-group-container'>
              <InputGroup
                name='name'
                label=''
                type='text'
                placeholder=''
                // maxLength='50'
                errors={errors}
                register={register}
                validationSchema={{
                  required: '',
                  minLength: {
                    value: 3,
                    message: '3',
                  },
                  maxLength: {
                    value: 50,
                    message: '50',
                  },
                }}
                watch={watch}
                limitLabel={true}
              />
            </div>
            <div className='textarea-group-container'>
              <div className='textarea-group grow-wrap'>
                <label htmlFor='introduction'></label>
                <textarea
                  type='textarea'
                  id='introduction'
                  name='introduction'
                  {...register('introduction', {
                    required: true,
                    maxLength: 160,
                  })}
                  placeholder=''
                  className={`desc-text-area ${
                    errors.introduction ? 'error' : ''
                  }`}
                  // value={profile.introduction || ''}
                />
              </div>
              <div className='error-message-group'>
                {errors.introduction &&
                  errors.introduction.type === 'required' && (
                    <span className='error'></span>
                  )}
                {errors?.introduction?.type === 'maxLength' && (
                  <span className='error'>160</span>
                )}

                <span className='limit-num'>
                  {watch('introduction') ? watch('introduction').length : '0'}
                  /160
                </span>
              </div>
            </div>
          </form>
        </div>
      </ModalContent>
    </>
  );
};

export default Modal;
